.footer {
  color: #fafafa;
}

.footer__box {
  padding: 30px;
  background-color: #22252a;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.footer__flex {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.footer__box2 {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 10px;
  letter-spacing: -0.01em;
  color: #22252a;
  background-image: url('../../img/footer-bg-mob@2x.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 50px;
}

@media screen and (min-width: 768px) {
  .footer__box {
    align-items: normal;
    padding: 60px;
    gap: 60px;
  }

  .footer__flex {
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
    gap: 60px;
  }

  .footer__box2 {
    font-size: 14px;
    line-height: 14px;
  }
}

@media screen and (min-width: 1024px) {
}
